<template>
  <main>
    <header class="header2">
      <h1 @click="addGoods()">
        
        <em class="number2" v-show='cart'>{{num}}</em>
      </h1>
    </header>
    <div class="content ">
      <div class="k_padding3">
        <div class="notice" v-show="true1">
          <span>500026000</span>
          <i @click="close()">X</i>
        </div>
        <!--  -->
        <div class="shoppingCart_empty" v-show='emptyCart'>
          <div class="cart_null">
            <div class="cart">
              <img src="../../../data/images/cart.png" alt="">
            </div>
            <span></span>
          </div>
        </div>
        <!--  -->
        <div class="shoppingCart" v-show='cart'>
          <div class="cart_goods">
            <div @click='changeCart()' :class="{tit2:true,cartCurrent:0==num2}">
              <img src="../../assets/images/cart1.png" alt="" class="select_icon cart1" v-show='cartIcon'>
              <img src="../../assets/images/cart2.png" alt="" class="select_icon cart2" v-show='cartIcon2'>
              <span></span>
            </div>
            <ul class="goodsList">
            	<li v-for="(item,i) in cartList1">
                <div class="select2" @click='changeCart2(i)'>
                  <img :src="item.cart1" alt="" class="select_icon2" v-show='cartList1[i].checked==false'>
                  <img :src="item.cart2" alt="" class="select_icon2" v-show='cartList1[i].checked==true'>
                </div>
                <div class="goodsInfo2">
                  <div class="goodsImg">
                    <img :src="item.pic" alt="">
                  </div>
                </div>
                <div class="goodsInfo3">
                  <h3>
                    <span class="saleType"></span>
                    <span class="goodsName">{{item.name}} </span>
                  </h3>
                  <p class="taxes">
                    
                    <span>{{item.taxes}}</span>
                  </p>
                  <div class="numberBox">
                    <span class="price4">{{item.price}}</span>
                    <div class="change_number">
                      <span class="sub" @click="sub(i)">-</span>
                      <span class="num">{{item.num}}</span>
                      <span class="add" @click="add(i)">+</span>
                    </div>
                  </div>
                  <div class="del">
                    <span></span>
                    <span>|</span>
                    <span></span>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
        <!--  -->
        <div class="content5 cart_down">
          <div class="blank2">
            <h2></h2>
          </div>
          <ul>
          	<li v-for="(item,i) in aList16">
          		<div class="c5_imgBox">
               <img :src="item.pic" alt="" class="c5_img1">
               <img :src="item.icon1" alt="" class="c5_img2">
               <img :src="item.icon2" alt="" class="c5_img3">
               <div v-if="i==0||i==1||i==3" class="c2_time">
                 &nbsp;{{countDownList}}
               </div>
             </div>
             <h3>{{item.desc}}</h3>
             <div class='express'>
               <span></span>
             </div>
             <div class="c2_price">
               <span class="newPrice">{{item.newPrice}}</span>
               <span class="oldPrice">{{item.oldPrice}}</span>
             </div>
                <span class="area">{{item.area}}</span>
          	</li>
          </ul>
        </div>
      </div>
      <div class="cartBlank"></div>
    </div>
    <!--  -->
    <div class="checkout" v-show='cart'>
      <div class="checkoutBox">
        <span>&nbsp;):</span>
        <span class="totalPrice">{{num*(89.00+7.80)}}</span>
        <div class="totalBox">
          <div class="total">
            
            <span>({{num}})</span>
          </div>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
  export default{
    data(){
      return{
        h:'',
        m:'',
        s:'',
        countDownList: '000000',
        actEndTime: '2050-11-19 18:50:00',
        true1:true,
        aList16:'',
        cartList1:'',
        // 
        num:1,
        num2:1,
        cart:false,
        emptyCart:true,
        cartIcon:false,
        cartIcon2:true,
        cartIcon3:false,
        cartIcon4:true,

      }
    },
    created() {
    this.countDown();
    },
    computed:{
      cartNum(){
        return this.cartList1[i].taxes+this.cartList1[i].price
      }
    },
    mounted() {
      this.$http.get('./data/index.json')
     .then((response)=>{
      this.aList16=response.data.aList16
     })
     this.$http.get('./data/cart.json')
     .then((response)=>{
      this.cartList1=response.data.cartList1
     })
     .catch(function(error){
       console.log(error)
     })
    },
    methods:{
      timeFormat(param) {
            return param < 10 ? '0' + param : param;
            },
            countDown(it) {
      var interval = setInterval(() => {
      // 
      let newTime = new Date().getTime();
      // 
      let endTime = new Date(this.actEndTime).getTime();
      let obj = null;
      // 
      if (endTime - newTime > 0) {
      let time = (endTime - newTime) / 1000;
      // 
      let day = parseInt(time / (60 * 60 * 24));
      let hou = parseInt(time % (60 * 60 * 24) / 3600);
      let min = parseInt(time % (60 * 60 * 24) % 3600 / 60);
      let sec = parseInt(time % (60 * 60 * 24) % 3600 % 60);
      obj = {
      day: this.timeFormat(day),
      hou: this.timeFormat(hou),
      min: this.timeFormat(min),
      sec: this.timeFormat(sec)
      };
            } else { // '00'
      obj = {
      day: '00',
      hou: '00',
      min: '00',
      sec: '00'
      };
      clearInterval(interval);
      }
            this.h=obj.hou;
            this.m=obj.min;
            this.s=obj.sec;
            this.countDownList = obj.hou + '' + obj.min + '' + obj.sec + '';
            }, 1000);
            },
      close(){
        this.true1=!this.true1;
      },
      add(i){
        this.cartList1[i].num++;
      },
      sub(i){
        if(this.cartList1[i].num>1){
          this.cartList1[i].num--;
        }
      },
      addGoods(){
        this.emptyCart=!this.emptyCart
        this.cart=!this.cart
      },
      changeCart(){
          this.cartIcon=!this.cartIcon
          this.cartIcon2=!this.cartIcon2
      },
      changeCart2(i){
        this.cartList1[i].checked=!this.cartList1[i].checked
      }


    }

  }
</script>

<style>
  @import url("../../assets/css/cart.css");
</style>



